import React, { useState } from 'react';
import { Link as RouterLink } from 'react-router-dom';
import clsx from 'clsx';
import PropTypes from 'prop-types';
import PerfectScrollbar from 'react-perfect-scrollbar';
import { makeStyles } from '@material-ui/styles';
import Pagination from '../Pagination';
import {
  Card,
  CardActions,
  CardContent,
  CardHeader,
  Checkbox,
  Divider,
  Link,
  Table,
  TableBody,
  TableCell,
  TableHead,
  TableRow,
  Typography,
  Button,
  colors
} from '@material-ui/core';

import { Label } from 'components';
import { pathUrls } from 'config';

const useStyles = makeStyles(theme => ({
  root: {},
  content: {
    padding: 0
  },
  inner: {
    minWidth: 700
  },
  nameCell: {
    display: 'flex',
    alignItems: 'center'
  },
  avatar: {
    height: 42,
    width: 42,
    marginRight: theme.spacing(1)
  },
  actions: {
    padding: theme.spacing(1),
    justifyContent: 'flex-end'
  },
  dangerButton: {
    marginRight: theme.spacing(1),
    color: colors.red[600],
    borderColor: colors.red[600],
    '&:hover': {
      borderColor: colors.red[900],
      color: colors.red[900]
    }
  },
  primaryButton: {
    marginRight: theme.spacing(1),
    color: colors.blue[600],
    borderColor: colors.blue[600],
    '&:hover': {
      borderColor: colors.blue[900],
      color: colors.blue[900]
    }
  },
  successButton: {
    marginRight: theme.spacing(1),
    color: colors.green[600],
    borderColor: colors.green[600],
    '&:hover': {
      borderColor: colors.green[900],
      color: colors.green[900]
    }
  }
}));

const Results = props => {
  const { className, count, page, pages, limit, regions, ...rest } = props;

  const classes = useStyles();

  const [selectedCustomers, setSelectedCustomers] = useState([]);

  const handleSelectAll = event => {
    const selectedCustomers = event.target.checked
      ? regions.map(region => region.title)
      : [];

    setSelectedCustomers(selectedCustomers);
  };

  const handleSelectOne = (event, title) => {
    const selectedIndex = selectedCustomers.indexOf(title);
    let newSelectedCustomers = [];

    if (selectedIndex === -1) {
      newSelectedCustomers = newSelectedCustomers.concat(
        selectedCustomers,
        title
      );
    } else if (selectedIndex === 0) {
      newSelectedCustomers = newSelectedCustomers.concat(
        selectedCustomers.slice(1)
      );
    } else if (selectedIndex === selectedCustomers.length - 1) {
      newSelectedCustomers = newSelectedCustomers.concat(
        selectedCustomers.slice(0, -1)
      );
    } else if (selectedIndex > 0) {
      newSelectedCustomers = newSelectedCustomers.concat(
        selectedCustomers.slice(0, selectedIndex),
        selectedCustomers.slice(selectedIndex + 1)
      );
    }
    setSelectedCustomers(newSelectedCustomers);
  };

  const clickActions = {
    onUpdate: () => {
      props.onSelect('update', selectedCustomers[0]);
    },

    onDelete: () => {
      props.onSelect('delete', selectedCustomers[0]);
      setSelectedCustomers([]);
    }
  };

  const btnViews = {
    update: (
      <Button
        className={classes.primaryButton}
        color="primary"
        onClick={clickActions.onUpdate}
        size="small"
        type="submit"
        variant="outlined"
      >
        Update
      </Button>
    ),
    delete: (
      <Button
        className={classes.dangerButton}
        color="primary"
        onClick={clickActions.onDelete}
        size="small"
        type="submit"
        variant="outlined"
      >
        Delete
      </Button>
    )
  };

  let btnView = null;
  if (selectedCustomers.length === 1) {
    btnView = (
      <React.Fragment>
        {btnViews.update}
        {btnViews.delete}
      </React.Fragment>
    );
  }
  return (
    <div
      {...rest}
      className={clsx(classes.root, className)}
    >
      <Typography
        color="textSecondary"
        gutterBottom
        variant="body2"
      >
        {count} Records found. Page {props.page} of {pages}
      </Typography>
      <Card>
        <CardHeader
          action={btnView}
          title="All Operation Regions"
        />
        <Divider />
        <CardContent className={classes.content}>
          <PerfectScrollbar>
            <div className={classes.inner}>
              <Table>
                <TableHead>
                  <TableRow>
                    <TableCell padding="checkbox">
                      <Checkbox
                        checked={selectedCustomers.length === regions.length}
                        color="primary"
                        indeterminate={
                          selectedCustomers.length > 0 &&
                          selectedCustomers.length < regions.length
                        }
                        onChange={handleSelectAll}
                      />
                    </TableCell>
                    <TableCell>Description</TableCell>
                    <TableCell>Radius</TableCell>
                    <TableCell>Search Radius</TableCell>
                    <TableCell>Latitide</TableCell>
                    <TableCell>Longitude</TableCell>
                  </TableRow>
                </TableHead>
                <TableBody>
                  {regions.map(region => (
                    <TableRow
                      hover
                      key={region.title}
                      selected={selectedCustomers.indexOf(region.title) !== -1}
                    >
                      <TableCell padding="checkbox">
                        <Checkbox
                          checked={
                            selectedCustomers.indexOf(region.title) !== -1
                          }
                          color="primary"
                          onChange={event =>
                            handleSelectOne(event, region.title)
                          }
                          value={selectedCustomers.indexOf(region.title) !== -1}
                        />
                      </TableCell>
                      <TableCell>
                        <div className={classes.nameCell}>
                          <div>
                            <Link
                              color="inherit"
                              component={RouterLink}
                              to={pathUrls.operationRegions.update.get_absolute_url(region.title)}
                              variant="h6"
                            >
                              {region.description}
                            </Link>
                            <div>
                              {region.center.latitide},{' '}
                              {region.center.longitude}
                            </div>
                          </div>
                        </div>
                      </TableCell>
                      <TableCell>{region.radius} Km</TableCell>
                      <TableCell>{region.searchRadius} Km</TableCell>
                      <TableCell>{region.center.latitide}</TableCell>
                      <TableCell>{region.center.longitude}</TableCell>
                    </TableRow>
                  ))}
                </TableBody>
              </Table>
            </div>
          </PerfectScrollbar>
        </CardContent>
        {pages !== 1 && (
          <CardActions className={classes.actions}>
            <Pagination
              count={count}
              limit={limit}
              onChangePage={props.onPageChange}
              onNextPage={props.onNextChange}
              onPrevPage={props.onPrevChange}
              page={page}
              pages={pages}
            />
          </CardActions>
        )}
      </Card>
    </div>
  );
};

Results.propTypes = {
  className: PropTypes.string,
  users: PropTypes.array.isRequired
};

Results.defaultProps = {
  users: []
};

export default Results;
