import React, { PureComponent } from 'react';
import { withStyles } from '@material-ui/styles';

import { Page } from 'components';
import { Header, Results } from './components';
import { connect } from 'react-redux';
import { Skeleton } from '@material-ui/lab';
import { pathUrls } from '../../../config';
import { withRouter } from 'react-router-dom';
import { regions } from '../utils';

const styles = theme => ({
  root: {
    padding: theme.spacing(3)
  },
  results: {
    marginTop: theme.spacing(3)
  }
});

class RegionsList extends PureComponent {
  state = {
    regions: null,
    page: 0,
    offset: 0,
    limit: 10,
    pages: 0,
    count: 0
  };

  componentDidMount() {
    const { match } = this.props;
    const { page } = match.params;
    const pageNo = page ? Number(page) : 1;
    if (pageNo) {
      let offset = (pageNo - 1) * this.state.limit;
      this.setState({ page: pageNo, offset: offset });
    }

    if (!this.state.regions && pageNo === 1) {
      this.onRegionsList(this.state);
    }
  }

  componentWillUpdate(nextProps, nextState) {
    if (
      this.state.page !== nextState.page &&
      this.state.offset !== nextState.offset
    ) {
      console.log(nextState);
      this.onRegionsList(nextState);
    }
  }

  onRegionsList(state) {
    regions.getRegionsList({
      state: state,
      setState: updatedState => this.setState(updatedState),
      headers: this.props.auth.headers
    });
  }

  changePageUrl = pageNo => {
    const { history } = this.props;
    let url = pathUrls.operationRegions.listPage.get_absolute_url(pageNo);
    history.push(url);
  };

  handlePageChange = (event, pageNo) => {
    let offset = (pageNo - 1) * this.state.limit;
    this.setState({ page: pageNo, offset: offset });
    this.changePageUrl(pageNo);
  };

  handleNextPage = () => {
    let page = this.state.page + 1;
    let offset = (page - 1) * this.state.limit;
    this.setState({ page: page, offset: offset });
    this.changePageUrl(page);
  };

  handlePrevPage = () => {
    let page = this.state.page - 1;
    let offset = (page - 1) * this.state.limit;
    this.setState({ page: page, offset: offset });
    this.changePageUrl(page);
  };

  handleMenuSelect = (type, user_id) => {
    const { history } = this.props;
    if (type === 'delete') {
      regions.deleteRegion({
        state: this.state,
        setState: updatedState => this.setState(updatedState),
        headers: this.props.auth.headers,
        id: user_id
      });
      console.log('Deleted', user_id);
    } else if (type === 'update') {
      history.push(pathUrls.operationRegions.update.get_absolute_url(user_id));
    }
  };

  loading = () => {
    let skelton = [];
    skelton.push(<Skeleton
      variant="text"
      width={150}
                 />);
    let data = Array(this.state.limit)
      .fill(1)
      .map(item => {
        return <Skeleton
          height={50}
          variant="text"
        />;
      });
    return [...skelton, ...data];
  };

  results = () => {
    const { classes } = this.props;
    if (this.state.regions) {
      return (
        <div>
          <Results
            className={classes.results}
            count={this.state.count}
            limit={this.state.limit}
            onMenuClick={this.handleMenuClick}
            onNextChange={this.handleNextPage}
            onPageChange={this.handlePageChange}
            onPrevChange={this.handlePrevPage}
            onSelect={this.handleMenuSelect}
            page={this.state.page}
            pages={this.state.pages}
            regions={this.state.regions}
          />
        </div>
      );
    } else {
      return this.loading();
    }
  };

  render() {
    const { classes } = this.props;

    return (
      <Page
        className={classes.root}
        title="Operation Regions List"
      >
        <Header />
        {this.results()}
      </Page>
    );
  }
}

const mapStateToProps = state => {
  return {
    auth: state.session.authentication
  };
};

export default withRouter(
  connect(mapStateToProps)(withStyles(styles)(RegionsList))
);
