import React from 'react'
import classes from './Pagination.module.css'

const Pagination = props => {
  const generate = (page, pages) => {
    const delta = 2
    const left = page - delta
    const right = page + delta + 1
    const pagination = []
    let prev = null
    Array(pages).fill(1).map((x, y) => {
      const no = x + y
      if (no === 1 || no === pages || (no >= left && no < right)) {
        return no
      } else {
        return null
      }
    }).filter((item) => {
      if (item) {
        if (prev) {
          if (item - prev === delta) {
            pagination.push(prev + 1)
          } else if (item - prev !== 1) {
            pagination.push('...')
          }
        }
        prev = item
        pagination.push(item)
        return item
      }
    })
    return pagination
  }
  const paginationnew = generate(props.page, props.pages).map((item) => {
    if (item === props.page) {
      return <li className={classes.active}>{item}</li>
    } else if (item === '...') {
      return <li className={classes.dot}>{item}</li>
    } else {
      return <li onClick={(event) => props.onChangePage(event, item)}>{item}</li>
    }
  })

  let prevBtn = null
  let nextBtn = null

  if (props.page < props.pages) {
    nextBtn = (<li onClick={props.onNextPage}>&raquo;</li>)
  } else {
    nextBtn = (<li className={classes.disable}>&raquo;</li>)
  }
  if (props.page > 1) {
    prevBtn = (<li onClick={props.onPrevPage}>&laquo;</li>)
  } else {
    prevBtn = (<li className={classes.disable}>&laquo;</li>)
  }

  return (
    <div className={classes.center}>
      <ul className={classes.pagination}>
        {prevBtn}
        {paginationnew}
        {nextBtn}
      </ul>
    </div>
  )
}

export default Pagination
